@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body, html, #root, .app, .container {
    width: 100%;
    min-height: 100%;
}

.container {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #d4ebf2;
}

.auth-form {
    font-family: 'Roboto Light', sans-serif;
    font-size: 28px;
    color: #292929;
    box-sizing: border-box;
    width: 100%;
    max-width: 400px;
    padding-left: 20px;
    padding-right: 20px;
}

form.input > input {
    font-family: 'Roboto', sans-serif;
    box-sizing: border-box;
    outline: none;
    border: solid 2px #f2dbd4;
    border-radius: 4px;
    color: #292929;
    width: 100%;
    padding: 12px;
    font-size: 14px;
    background: rgba(255, 255, 255, 1);
    margin-top: 0.5em;
}

.display-none {
    display: none;
}

#post-response-errors > ul {
    display: table;
    font-size: 12px;
    margin: 0 auto;
    padding-top: 1em;
}

.submit-button-padding {
    padding-top: 8px;
}

.btn {
    width: 100%;
    min-width: 100px;
    color: #565656;
    padding: 12px;
    font-size: 14px;
    font-weight: bold;
    font-family: 'Roboto', sans-serif;
    border: solid 2px #f2dbd4;
    border-radius: 4px;
    background: #f2dbd4;
    cursor: pointer;
}

.ml-auto {
    margin-left: auto;
}

.mr-auto {
    margin-right: auto;
}

/*============== CSS FOR THE FOLLOW USER UI ==============*/

.user-grid {
    display: grid;
    align-items: center;
    grid-template-columns: repeat(auto-fit, minmax(15em, 1fr));
    justify-content: center;
    justify-items: center;
    max-width: 80%;
    margin: 3em auto 0;
}

.follow-node {
    font-family: 'Roboto', sans-serif;
    position: relative;
    width: 5em;
    height: 5em;
    padding: 20%;
    border-radius: 25px;
    background-color: #F5F5F5;
    margin: 0.6em;
    font-size: 20px;
    text-align: center;
}

.follow-button {
    background-color: #fcba03;
    border: none;
    border-radius: 25px;
    color: white;
    padding: 10% 20%;
    text-align: center;
    opacity: 0.6;
    visibility: hidden;
}

.follow-node:hover .follow-button {
    visibility: visible;
}

.follow-button:hover {
    opacity: 1;
}

.follow-node.follow-node-clicked {
    background-color: #97e68e;
}

.follow-node.follow-node-clicked > .follow-button {
    visibility: visible;
    opacity: 1;
}


/*============== CSS FOR THE FEED POST ==============*/

.post-card {
    width: 35%;
    border: 3px solid #add8e6;
    background-color: #DAE0E3;
    margin-bottom: 0.25em;
    border-radius: 1em;
    margin-top: 2em;
}

#feed-grid > Post:first-child {
    margin-top: 4em;
}

.post-header {
    display: flex;
    align-items: center;
    padding-left: 10px;
}

.post-username {

}

.post-image {
    height: auto;
    width: 100%;
    border-top: 3px solid #add8e6;
    border-bottom: 3px solid #add8e6;
}

.post-text {
    padding-left: 10px;
}

.post-avatar {
    margin-right: 10px;
}

.container-post {
    padding-top: 2em;
}

/*============== CSS FOR THE POST UPLOAD UI ==============*/

.post-form-main {
    font-family: "Roboto Light", sans-serif;
    background: #fff;
    padding: 20px;
    border-radius: 25px;
    text-align: center;
    margin: 40px
}

#sub {
    margin-top: -10px;
    color: #777;
}

.box-upload {
    display: grid;
    place-items: center;
    border: 1px dashed #777;
    padding: 25px 150px;
    border-radius: 25px;
    margin-top: 20px;
    margin-bottom: 40px;
}

.image-upload {
    display: flex;
    flex-wrap: wrap;
}

.img-label {
    cursor: pointer;
}

.image-upload:hover {
    opacity: .8;
}

.img-input {
    display: none;
}

.image-preview {
    position: relative;
}

.close-icon {
    width: 25px;
    background: #fff;
    border-radius: 5px;
    opacity: .8;
    position: absolute;
    z-index: 10;
    right: 15px;
    top: 20px;
    cursor: pointer;
}

#uploaded-image {
    max-height: 250px;
    width: auto;
    border-radius: 10px;
}

.close-icon:hover {
    opacity: 1;
}

.post-form {
    font-family: "Roboto Light", sans-serif;
    font-size: 17px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.post-btn {
    width: 100%;
    min-width: 100px;
    color: #565656;
    padding: 12px;
    font-size: 14px;
    font-weight: bold;
    border: solid 2px #f2dbd4;
    border-radius: 4px;
    background: #f2dbd4;
    cursor: pointer;
}

.text-area {
    display: grid;
    place-items: center;
    margin-top: 10px;
    margin-bottom: 40px;
}

.description-input {
    font-family: 'Roboto', sans-serif;
    box-sizing: border-box;
    outline: none;
    border: solid 2px #f2dbd4;
    border-radius: 25px;
    color: #292929;
    width: 100%;
    padding: 12px;
    font-size: 14px;
    background: rgba(255, 255, 255, 1);
    min-height: 100px;
    max-height: 140px;
    min-width: 300px;
    max-width: 700px;
    margin-bottom: 20px;
}


/*============== CSS FOR THE NAVBAR ==============*/

.navbar-container {
    border-bottom: 3px solid lightblue;
    height: 4em;
}

.navbar {
    display: grid;
    height: 100%;
    margin: 0 10em;
    justify-content: center;
    grid-template-columns: 1fr 1fr;
    align-items: center;
}

.logo {
    font-family: "Brush Script MT", serif;
    font-size: 32px;
    text-decoration: none !important;
    -webkit-text-decoration-color: black;
            text-decoration-color: black;
}

.user-status {
    display: flex;
}

.user-status div:first-child {
    margin-right: 1em;
}


/*============== CSS FOR COMMENTS SECTION ==============*/

.comments-overlay {
    position: static;
    height: 100%;
    width: 100%;
    background: rgba(128,128,128,0.75);
    z-index: 1;
}

.comment-close {
    color: #F0F0F0;
    position: absolute;
    right:15px;
    top:15px;
}

.comment-card {
    font-family: 'Roboto Light', sans-serif;
    border-radius: 1em;
    background-color: #F0F0F0;
    width: 800px;
    height: 600px;
    margin: auto;
    margin-top: 200px;
    display:flex;
}
.comment-section {
    width:100%;
}

.comment-avatar {
    margin-right: 10px;
}

.comment-header{
    display: flex;
    padding: 0.5em;
    align-items: center;
    border-bottom: 1px solid rgba(220,220,220,0.7);
}

.comment-replies {
    display: flex;
    padding: 0.5em;
    align-items: center;
}

.comment-text {
    padding: 0.5em;
    align-items: center;
    margin-top: 400px;
}

.comment-image {
    height: 600px;
    width: 400px;
    object-fit: cover;
    border-radius: 1em;
}

.comment-form {
    display: flex;
    flex: 1 1;
    font-family: 'Roboto Light', sans-serif;
    font-size: 28px;
    color: #292929;
    box-sizing: border-box;
    width: 100%;
}

